<script >
  // import { RouterLink, RouterView } from "vue-router";
  import { onMounted, created } from "vue";

  import Navbar from "./components/Navbar.vue";
  import Quotes from "./components/Quotes.vue";
  import Modal from "./components/Modal.vue";
  import AddButton from "./components/AddButton.vue";

  import axios from "axios";
  import { doc, addDoc, deleteDoc, updateDoc, collection, onSnapshot, QuerySnapshot } from "@firebase/firestore";
  import { db } from "./firebase";
  import $store from './store'

  export default{
    components: {
      Navbar,
      Quotes,
      Modal,
      AddButton
    },
    data(){
      return {
        currentId: 1,
        modalOpen: false,
         quotes: [],
         edit: false,
         editquote: {},
         search: '',
         quoteId: Number
      }
    },
    created(){
      // this.getquotes()
      
    //     try {
    //   const res = await axios.get(`http://localhost:3000/quotes`);
    //   this.quotes = res.data;
    //   console.log(this.quotes );
    //   this.quoteId = this.quotes.length;
    //   console.log(this.quoteId);
    // } catch (error) {
    //   console.log(error);
    // }      
    
        onSnapshot(collection(db, 'quotes'), (QuerySnapshot) => {
             const fbquotes = [];
             QuerySnapshot.forEach((doc) => {
              // $store.state.firebaseId = doc.id
              const quote = {
                id: doc.data().quoteId,
                quoteText: doc.data().quoteText,
                quoteAuthor: doc.data().quoteAuthor,
                quoteGenre: doc.data().quoteGenre,
                timeCreating: doc.data().timeCreating,
                timeEditing: doc.data().timeEditing,
                firebaseId: doc.id
              }
              fbquotes.push(quote)
              // console.log(quote.firebaseId);
              $store.state.arrFirebaseIds.push(quote.firebaseId)
              // console.log($store.state.arrFirebaseIds);
             })
             
             this.quotes = fbquotes
            //  console.log(this.quotes);
        })
        // console.log(this.quotes);
    },
    onMounted(){
       
    },
    computed: {
      filterquotes(){
        return this.search ? 
        this.quotes.filter(quote => {
          for(let key in quote){
            if(String(quote[key]).toLowerCase().includes(this.search.toLowerCase())){
              return quote;
            }
          }
        })
        : this.quotes;
      },
   },
    methods: {
      openModal(){
        this.modalOpen = true
      },
      closeModal(){
        this.modalOpen = false
        this.edit = false
      },
      async addquote(item){
        let addedQuoteId;
        if(item.id <= this.quoteId){
          addedQuoteId = this.quoteId++;
              // console.log(addedQuoteId);
        }else{
          addedQuoteId = item.id
        }
        // const getItems = await axios.get(`http://localhost:3000/quotes`);
      // this.quotes = res.data;
      //   const res = await axios.post(`http://localhost:3000/quotes`, {
      //   // 
      // //  id: item.id ,
      //  quoteText: item.quoteText,
      //  quoteAuthor: item.quoteAuthor,
      //  quoteGenre: item.quoteGenre,
      //  timeCreating: item.timeCreating
      // });
          await addDoc(collection(db, 'quotes'), {
            quoteId: addedQuoteId,
            quoteText: item.quoteText,
            quoteAuthor: item.quoteAuthor,
            quoteGenre: item.quoteGenre,
            timeCreating: item.timeCreating
          })
          // this.quotes.push(item)
          // console.log(this.quotes);
          // console.log(item.id);
          // this.quotes = [...this.quotes, res.data];
          this.modalOpen = false
      },
      deletequote(id){
        console.log(id);
        deleteDoc(doc(collection(db, 'quotes'), String(id) ))
        // axios.delete(`http://localhost:3000/quotes/${id}`)
          // let index = this.quotes.findIndex(quote => quote.id == id)
          // this.quotes.splice(index, 1)
      },
      changequote(id){
       $store.state.firebaseId = id
      //  $store.state.firebaseId.toString()
      this.edit = this.modalOpen = true
      let pickedquote = this.quotes.find(quote => quote.id == id)
      this.editquote = pickedquote
      // await updateDoc(doc(collection(db, 'quotes'), String(id), {
      //   quoteText : quoteEdited.quoteText,
      //   quoteAuthor : quoteEdited.quoteAuthor,
      //   quoteGenre : quoteEdited.quoteGenre,
      //   timeEditing : quoteEdited.timeEditing
      // }))
      },
      async editedquote(quoteEdited, id){
      // console.log(typeof $store.state.firebaseId);
        // try{
          await updateDoc(doc(collection(db, 'quotes'), $store.state.firebaseId), {
        quoteText : quoteEdited.quoteText,
        quoteAuthor : quoteEdited.quoteAuthor,
        quoteGenre : quoteEdited.quoteGenre,
        timeEditing : quoteEdited.timeEditing
      }
      )
      
        // }catch(e){
        //   console.log(e);
        //   // console.log(typeof $store.state.firebaseId);
        // }
        

        // try{
        //     await axios.patch(`${`http://localhost:3000/quotes`}/${quoteEdited.id}`, {
        //       quoteAuthor: quoteEdited.quoteAuthor,
        //      quoteGenre: quoteEdited.quoteGenre,
        //      quoteText: quoteEdited.quoteText,
        //      timeEditing: quoteEdited.timeEditing
        //     })
        //    }
        //    catch (error) {
        //    console.error(error);
        //    }
        this.quotes.forEach(quote => {
          if(quote.id == quoteEdited.id) {
            quote.quoteText = quoteEdited.quoteText
            quote.quoteAuthor = quoteEdited.quoteAuthor
            quote.quoteGenre = quoteEdited.quoteGenre
            quote.timeEditing = quoteEdited.timeEditing
            // this.timeCreating =  quoteEdited.timeCreating
          }
         })
      },
      getquotes(){
        const localquotes = localStorage.quotes
        if(localquotes){
          this.quotes = JSON.parse(localquotes)
        }
      },
     async random(){
        let randomIndex = Math.round(Math.random() * (this.quotes.length - 1 ) + 1);
        console.log(randomIndex);
        // console.log(this.quotes.length);
        // const res = await axios.get(`http://localhost:3000/quotes`);
        // this.quotes = res.data;
        // this.quotes = this.quotes
        // for(let i = 0; i < this.quotes.length; i++){
        //   if(i == randomIndex){
        //     $store.state.randomFirebaseId = this.quotes[randomIndex].id
        //     console.log(this.quotes);
        //     console.log(this.quotes[randomIndex]);
        //   }
        // }
        // console.log($store.state.randomFirebaseId);
        this.quotes = this.quotes.filter(quote => quote.id == randomIndex)
        // console.log(this.quotes);
        // return this.quotes;        
       },
       async allquotes(){
        onSnapshot(collection(db, 'quotes'), (QuerySnapshot) => {
             const fbquotes = [];
             QuerySnapshot.forEach((doc) => {
              // $store.state.firebaseId = doc.id
              const quote = {
                id: doc.data().quoteId,
                quoteText: doc.data().quoteText,
                quoteAuthor: doc.data().quoteAuthor,
                quoteGenre: doc.data().quoteGenre,
                timeCreating: doc.data().timeCreating,
                timeEditing: doc.data().timeEditing,
                firebaseId: doc.id
              }
              fbquotes.push(quote)
              // console.log(quote.firebaseId);
              $store.state.arrFirebaseIds.push(quote.firebaseId)
              // console.log($store.state.arrFirebaseIds);
             })
             
             this.quotes = fbquotes
            //  console.log(this.quotes);
        })
       }
    },
    watch: {
      quotes: {
        handler(newquotes){
          localStorage.quotes = JSON.stringify(this.quotes)
        },
        deep: true
      }
    }
  }
</script>
<template>
  <Navbar 
  @searchValue="search = $event"
  />
  <Quotes 
  :quotes="filterquotes"
  @delquote="deletequote" 
  @changequote="changequote"
  :search="search"
  @random="random"
  @allquotes="allquotes"
  />
  <Modal 
  :quotes="quotes"
  :edit="edit"
  :editquote="editquote"
  @addquote="addquote"
  :currentId="currentId" 
  v-show="modalOpen" 
  @closeModal="closeModal" 
  @editedquote="editedquote"
  />
  <AddButton 
  @openModal="openModal" 
  />
  <!-- <RouterView /> -->
</template>






